import React, { useEffect, useRef, useState } from "react";

import Card from 'react-bootstrap/Card';
import Button from "react-bootstrap/Button";

import clsx from "clsx";

import "./index.css";
import exp from "constants";

type GroupIntroCardProps = {
  name: string,
  image: string,
  description: string,
  website: string,
  twitter: string
}

const Group = () => {
  const MakeGroupIntroCard = ({ group }: { group: GroupIntroCardProps }) => {
    const cardRef = useRef<HTMLDivElement>(null);
    const [isVisible, setIsVisible] = useState(false);

    useEffect(() => {
      const checkScroll = () => {
        if (!cardRef.current) {
          return;
        }

        const rect = cardRef.current.getBoundingClientRect();
        if (rect.top <= window.innerHeight) {
          setIsVisible(true);
        }
      };
      checkScroll();

      window.addEventListener("scroll", checkScroll);
      return () => window.removeEventListener("scroll", checkScroll);
    }, []);

    return (
      <div
        ref={cardRef}
        className={clsx(
          isVisible ? "card-visible" : "card-invisible",
          "card-fade-in"
        )}
      >
        <Card className="card">
          <Card.Img className="p-3" variant="top" src={group.image} />
          <Card.Body>
            <Card.Title>{group.name}</Card.Title>
            <Card.Text
              style={{ whiteSpace: "pre-wrap" }}
            >
              {group.description}
            </Card.Text>
            <Button className="m-1" variant="success" href={group.website} target="_blank">ホームページ</Button>
            <Button className="m-1" variant="primary" href={group.twitter} target="_blank">𝕏(Twitter)</Button>
          </Card.Body>
        </Card>
      </div>

    )
  }
  const groupIntroCards = [
    {
      name: "工学研究部",
      image: "/logo/kouken.jpeg",
      description: `工学研究部は大学公認の、部員が一人ひとり作りたいものを作る技術系サークルです。
ハードウェアからソフトウェアまで工学分野において幅広く活動しており、Maker Faire Tokyoや技術書典に参加しています。


活動日：いつでも
部会：毎週金曜日18:00`,
      website: "https://www.koken.club.uec.ac.jp/about.html",
      twitter: "https://twitter.com/ueckoken"
    },
    {
      name: "team411",
      image: "/logo/team411.png",
      description: `team411は大学生活と社会の「もやもや」を解決する、ITソリューションチーム
team411は大学生が「自分たちの身の回りの課題」を解決するために、IT技術を用いてプロダクトを開発する電気通信大学のチームです。メンバーが自主的にプロジェクトを立ち上げ行動しており、非常にのびのびと活動ができるサークルです。
未経験者でも研修でプログラミング等の知識をいち早くつけることができます。`,
      website: "https://www.team411.jp/",
      twitter: "https://twitter.com/team411_uec"
    },
    {
      name: "MMA",
      image: "/logo/mma.png",
      description: `こんにちは! 電気通信大学 MMA です!
1975年から続くサークルで、ソフトウェアやネットワークまわりの研究・開発などを主に、計算機に関わることだったらなんでも活動しています!
合宿では煙火打揚従事者の資格をとったうえで、本格的な花火の打ち上げを毎年行っています!
お気軽にお越しください!`,
      website: "https://wiki.mma.club.uec.ac.jp/",
      twitter: "https://twitter.com/uecmma"
    },
    {
      name: "インタラクティブメディアラボ(IML)",
      image: "/logo/iml.png",
      description: `IMLでは入学から半年間、先輩方から基礎的なものづくりテクニックを学びます。その後、全員が現役研究者を交えて徹底的に議論を行い、学術的にも面白く、これまでみたこともない、プロジェクトを立ち上げます。さらに学会発表などの「長期的」かつ「専門的」な「高い目標設定」をかかげ、自由に研究開発を行います。`,
      website: "https://imedia-lab.net/",
      twitter: "https://twitter.com/iMedia_Lab"
    },
    {
      name: "ロボメカ工房",
      image: "/logo/rmf.png",
      description: `ロボメカ工房はものづくりを行う大学公認のサークル的活動をする団体です。大学からの設備提供のもと、快適な環境が整っています。いくつかのチームに分かれ、各種大会への出場や出展を目標としながら、学生同士協力して活動しています。興味のあるチームに入って仲間と一緒にロボットを作りましょう！`,
      website: "https://sites.google.com/view/uec-rmf/home/",
      twitter: "https://twitter.com/robomech_uec"
    },
    {
      name: "X680x0同好会",
      image: "/logo/x680x0.png",
      description: `X680x0同好会はゲーム制作を中心にプログラミングや楽曲制作、イラスト作成を行うサークルです。未経験の方でも、講座を通して一緒に制作の基礎を学ぶことができます。
ゲーム制作・イラスト・作曲に興味のある方はぜひX680x0同好会へお越し下さい！`,
      website: "https://www.x68uec.org/",
      twitter: "https://twitter.com/x68uec"
    },
    {
      name: "VLL",
      image: "/logo/vll.png",
      description: `バーチャルライブ研究会はバーチャルシンガーのファンメイドライブの制作・運営を行っているサークルです。計16班に分かれてバーチャルライブの制作に関する幅広い活動を行っています。例年前学期には部内ライブ、後学期にはMIKUECを開催しておりMIKUECは毎年多くのお客さんに楽しんでいただいています。`,
      website: "https://mikuec.com/",
      twitter: "https://twitter.com/uec_VL_Lab"
    },
    {
      name: "U.E.C.wings",
      image: "/logo/wings.png",
      description: `私たち電気通信大学U.E.C.wingsは空を飛ぶものを技術で作り上げるサークルです．
昨年は鳥人間コンテスト人力飛行機部門でチーム記録を更新することに成功しました！！！
皆様の好奇心がかられるような体験がここにはあります！！
部員一同皆様の入部をお待ちしています！！！`,
      website: "https://www.uecwings.club.uec.ac.jp/blog/",
      twitter: "https://twitter.com/uecwings2024"
    },
    {
      name: "無線部",
      image: "/logo/musen.jpg",
      description: `無線部は主にアマチュア無線のコンテストに参加し、全国規模の大会で実績を残しています。アマチュア無線は個人の趣味で行われる無線通信であり、自らの設備を用いて世界中の相手と交信できることが魅力です。コンテストでの勝利を目指すために、高性能なアンテナの製作やソフトウェアの開発なども行います。`,
      website: "https://www.ja1zgp.com/clubguide/",
      twitter: "https://twitter.com/ja1zgp"
    },
    {
      name: "TeRes",
      image: "/logo/teres.jpg",
      description: `私たちTeResは、プログラミング・イラスト・DTMの3班を主体に、各々が幅広い分野で活動しながら、互いに技術を提供し助け合って"モノ"を作っていくよろず創作サークルです。 マルチに創作活動を楽しみたい方､夜間昼間学部を問わず歓迎です！`,
      website: "http://www.teres.club.uec.ac.jp/",
      twitter: "https://twitter.com/teresUEC"
    },
  ];
  return (
    <>
      <div className="cardContainer">
        {groupIntroCards.map((group) => (
          <MakeGroupIntroCard {...{ group }} key={group.name} />
        ))}
      </div>
    </>
  )
}

export default Group